import React from "react";
import "./App.css";
import Results from "./Results";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      searchQuery: "",
      perPage: 100,
      toFetch: "",
      page: 0, // Total of 45,716 results
      results: []
    };
  }

  async componentDidMount() {
    this.setState({
      toFetch: `https://data.nasa.gov/resource/gh4g-9sfh.json?$limit=${
        this.state.perPage
      }&$offset=0`
    });
    let results = await fetch(
      `https://data.nasa.gov/resource/gh4g-9sfh.json?$limit=${
        this.state.perPage
      }&$offset=0`
    );
    console.log(results);
    results = await results.json();
    this.setState({ results });
  }

  onChangeSearch = e => {
    e.preventDefault();
    this.setState({
      searchQuery: e.target.value
    });
  };

  onChangePerPage = e => {
    e.preventDefault();
    this.setState({ perPage: e.target.value, page: 0 });
  };
  onChangePage = e => {
    e.preventDefault();
    this.setState({ page: e.target.value - 1 });
  };
  search = e => {
    e.preventDefault();
    this.setState({
      toFetch: `https://data.nasa.gov/resource/gh4g-9sfh.json?$limit=${
        this.state.perPage
      }&$offset=${this.state.page}000&$where=lower(name)%20like%20lower(%22%25${
        this.state.searchQuery
      }%25%22)`
    });
    fetch(
      `https://data.nasa.gov/resource/gh4g-9sfh.json?$limit=${
        this.state.perPage
      }&$offset=${this.state.page}000&$where=lower(name)%20like%20lower(%22%25${
        this.state.searchQuery
      }%25%22)`
    )
      .then(r => r.json())
      .then(results => this.setState({ results }));
  };

  render() {
    return (
      <div className="App">
        {this.state.scrollY > 70 ? (
          <div className="header" style={{ position: "fixed" }}>
            Meteorite Explorer
          </div>
        ) : (
          <div className="header">Meteorite Explorer</div>
        )}
        <form className="searchBar" onSubmit={this.search}>
          <div className="searchDiv">
            Name{" "}
            <input
              value={this.state.searchQuery}
              type="text"
              name="search"
              className="searchInput"
              placeholder="Search by Name"
              onChange={this.onChangeSearch}
            />{" "}
            Page{" "}
            <input
              type="number"
              name="perPage"
              class="pageInput"
              placeholder="Results per Page"
              value={this.state.page + 1}
              onChange={this.onChangePage}
              min={1}
            />{" "}
          </div>

          <button className="searchButton"> Search</button>
        </form>

        <div className="perPageContainer">
          Results Per Page:{" "}
          <input
            type="number"
            name="perPage"
            class="perPageInput"
            placeholder="Results per Page"
            value={this.state.perPage}
            onChange={this.onChangePerPage}
          />
        </div>
        <Results results={this.state.results} />
      </div>
    );
  }
}

export default App;
